#myTable_wrapper {
  min-width: 95%;
  max-width: 95%;
  margin: 0 auto;
}

#message {
  color: var(--color3);
  margin: 0 auto;
}

.rank-image {
  height: 2em;
}

#myTable {
  color: var(--foreground);
  border-collapse: separate;
  border-spacing: 0 1em;
  text-align: center;
}

thead {
  color: var(--foreground);
}

.table-head th {
  background: var(--background);
  border-radius: 15px;
}

.table-controls {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

div.dt-search {
  text-align: center;
  width: max-content;
  margin: 0 auto;
  display: inline;
}

.dt-search label {
  color: var(--foreground);
  margin: 0 auto;
}

div.dt-container .dt-search input {
  background-color: var(--foreground);
  border-radius: 15px;
  margin-left: 1%;
}

.toggle-fail {
  width: max-content;
  margin: auto;
  display: inline;
}

.toggle-fail label, div#myTable_info.dt-info {
